body {
  margin: 0;
}

html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--active-color);
}
